define('public/router', ['exports', 'ember', 'public/config/environment'], function (exports, Ember, config) {

	'use strict';

	var Router = Ember['default'].Router.extend({
		location: config['default'].locationType
	});

	Router.map(function () {
		this.route('map');
		this.route('about');
		this.route('docs');
	});

	exports['default'] = Router;

});